export default {
  name: 'shcontainer',

  props: {
    /**
       * Centres the content using `margin` by giving it a width of `80vw`.
    */
    centered80: Boolean,
    /**
       * Centres the content using `margin` by giving it a width of `90vw`.
    */
    centered90: Boolean,
    /**
       * Aligns the text content to the centre.
    */
    textCenter: Boolean,
    /**
       * Shows grid gaps. For testing purposes.
    */
    gridGap: Boolean
  }
}
